@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{width:100vw;height:100vh;background-color:black;display:flex;justify-content:center;align-items:center}

.max{width:800px;margin:0}.max p{width:100%;font-size:12.5px;font-family:VT323;word-break:break-all;color:red;animation-name:text;animation-duration:15s}.max p:focus{outline:none}@keyframes text{from{font-size:5px;color:white}to{font-size:12.5px;color:red}}

